import React from "react";
import "./PageFAQ.css";

function Faq() {
    return (
        <div>
            <section>
                <h1>dition 2023</h1>
                <h2>Foire aux questions - FAQ</h2>
                <br/>
                <h3>Est-ce que le stage est obligatoire?</h3>
                <li>Le stage de fin d'tudes en informatique est obligatoire pour l'obtention du diplme collgial.</li>

                <h3>Quel est l'horaire de l'tudiant durant les stages?</h3>
                <li>L'tudiant doit respecter l'horaire de l'entreprise durant son stage.</li>

                <h3>Est-ce que l'tudiant travaille pendant les journes pdagogiques et les journes de rattrapage?</h3>
                <li>L'tudiant doit respecter l'horaire de l'entreprise durant son stage et ce mme durant les journes pdagogiques et de rattrapage.</li>

                <h3>Quelle est la dure d'un stage de fin d'tudes?</h3>
                <li>La dure du stage est de 15 semaines pour les deux profils de sortie (rseaux et programmation).</li>

                <h3>Quelles sont les dates prvues pour les stages?</h3>
                <li>Les stages sont prvus du 21 janvier au 3 mai 2019.</li>

            </section>
        </div>
    );
  }
  
  export default Faq;